
// BOOK MODAL WINDOW ----- Common styles
  .block-scroll {
    overflow: hidden;
  }
  .autorization-modal-backdrop {
    position: fixed;
    top: 0;
    left: 0;
    padding: 70px 40px 40px 40px;
    width: 100%;
    height: 100%;    
    background-color: var(--accent-color1);
    z-index: 1100;
    overflow: auto;
  }
  .autorization-modal-backdrop.is-hidden {
    display: none;
    // opacity: 0;
    // pointer-events: none;
  }

  .modal{
    position: relative;
    padding: 80px 40px 40px 40px;
    z-index: 1000;
    background-color: var(--light-theme-background);
    border-radius: 18px;  
  }
  .close-button {
    position: absolute;
    padding: 0px;
    display: flex;
    justify-content: center;
    align-items: center;
    cursor: pointer;
    background-color: transparent;
    border-color: transparent;
  }
  .icon-x {
    width: 100%;
    height: 100%;
    stroke: var(--text-dark-color);

    &:hover,
    &:focus{
      stroke: var(--accent-color1);
    }
  }

  .modalForm-fields-list{
    display: flex;
    flex-direction: column;
  }
  .modalForm-inputWrap {
    position: relative;

    &:hover,
    &:focus{

      .modalForm-input{
        border-color: var(--accent-color1);
        outline: none;
        border-color: var(--accent-color1);
        color: var(--text-dark-color); 

        &::placeholder{
          color: var(--accent-color1);
        }

        &::selection{
          color: currentColor;
          background-color: var(--accent-color2);
        }
      }

      .modalForm-icon.name,
      .modalForm-icon.name{
        fill : var(--accent-color1);
      }
      .modalForm-icon.email,
      .eye-btn{
        stroke : var(--accent-color1);
      }

    }
  }


  //----------------------------------------
  .modalForm-input {
    width: 100%;
    font-weight: 700;
    line-height: 1;
    text-transform: uppercase;
    border: 2px solid var(--text-dark-color);
    background-color: transparent;
    border-radius: 40px;
    color: var(--text-dark-color);
    transition: border-color var(--transition), color var(--transition);

    &::placeholder{
      color: currentColor;
      transition: color var(--transition);
    }
  }

  input:-webkit-autofill,
  input:-webkit-autofill:hover,
  input:-webkit-autofill:focus,
  textarea:-webkit-autofill,
  textarea:-webkit-autofill:hover,
  textarea:-webkit-autofill:focus,
  select:-webkit-autofill,
  select:-webkit-autofill:hover,
  select:-webkit-autofill:focus {
      -webkit-text-fill-color: var(--text-dark-color);
      transition: background-color 5000s ease-in-out 0s;
  } 

  //--------------------------------------- 
  .modalForm-icon.name{
    position: absolute;
    top: 50%;
    margin-left: 20px; 
    transform: translateY(-50%);
    fill : var(--text-dark-color);
    stroke : "none";
    transition: fill var(--transition), stroke var(--transition);
  }
  .modalForm-icon.email{
    position: absolute;
    top: 50%;
    margin-left: 20px; 
    transform: translateY(-50%);
    fill : none;
    stroke : var(--text-dark-color);
    transition: fill var(--transition), stroke var(--transition);
  }
  .eye-btn{
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
    right: 34px;
    width: 30px;
    height: 30px;
    margin-left: 20px;
    padding: 0;
    display: flex;
    align-items: center;
    justify-content: center;
    border: none;
    background-color: transparent;
    fill: var(--text-dark-color);
    stroke: var(--text-dark-color);
    stroke-width: 0.9;
    transition: fill var(--transition), stroke var(--transition);
    cursor: pointer;
  }
  .close-eye,
  .open-eye{
    width: 100%;
    height: 100%;
  }

  //---------------------------------------
  .modalWrapper-btn {
    display: block;
  }
  .submitBtn{
    display: block;
    width: 100%;
    padding: 16px 0;
    font-size: 16px;
    font-weight: 700;
    color: var(--logo-theme-dark);
    background-color: var(--dark-theme-background);
    border: none;
    border-radius: 40px;
    cursor: pointer;
    transition: background-color var(--transition);
  
    &:hover,
    &:focus {
      background-color: var(--accent-color1);
    }
  }
  .btn-signUpIn-ul {
    display: flex;
    justify-content: center;
    column-gap: 20px;
  }
  .btn-sign-up,
  .btn-sign-in {
    position: relative;
    padding: 0;
    font-weight: 700;
    font-size: 14px;
    white-space: nowrap;
    color: var(--light-theme-fifty);
    background-color: transparent;
    border: none;
    cursor: pointer;
    transition: color var(--transition);

    &:hover,
    &:focus {
      color: var(--accent-color1);
    }
    &.current {
      color: var(--accent-color1);
    }
    &.current::after {
      position: absolute;
      width: 100%;
      height: 1px;
      content: '';
      left: 0;
      bottom: -1px;
      background: var(--accent-color1);
    }
  }



// BOOK MODAL WINDOW ----- Mobile styles
//{
  @media screen and (max-width: 767.8px) {
    .autorization-modal-backdrop {
      padding-left: 20px;
      padding-right: 20px;
    }
    .modal{
      position: relative;
      top: 50%;
      left: 50%;
      max-width: 335px;
      width: 100%;
      padding: 40px 20px;
      transform: translate(-50%, -50%);
    }
    .close-button {
      top: 14px;
      right: 14px;
      width: 20px;
      height: 20px;
    }
    .modalForm-fields-list{
      row-gap: 24px;
    }
    .modalForm-input {
      padding: 16px 40px 16px 24px;
      font-size: 16px;
    }  
    .modalForm-input::placeholder {
      font-size: 16px;
    }
    .modalForm-icon.name {
      width:  13px;
      height: 18px;
      right: 24px;
    }
    .modalForm-icon.email {
      width:  15px;
      height: 12px;
      right: 24px;
    }
    .eye-btn {
      width: 21px;
      height: 21px;
      right: 24px;
    }
    .modalWrapper-btn {
      margin-top: 24px;
    }
    .submitBtn {
      font-size: 16px;
    }
    .btn-signUpIn-ul {
      margin-top: 34px;
      font-size: 14px;
      line-height: 1.28;
    }
  }
  @media screen and (max-width: 270px) {
    .modalForm-input {
      padding: 14px 35px 14px 16px; 
      font-size: 12px;
    } 
    .modalForm-input::placeholder {
      font-size: 11px;
    }
    .modalForm-icon {
      right: 16px;
    }
    .submitBtn{
      padding: 14px 0;
      font-size: 12px;
    }
    .btn-signUpIn-ul {
      font-size: 10px;
      line-height: 1.28;
    }
}
//}


// BOOK MODAL WINDOW ----- Tablet styles
//{  
  @media screen and (min-width: 768px) {
    .modal {
      position: absolute;
      width: 579px;
      display: flex;
      flex-direction: column;
    }
    .modalForm-fields-list{
      row-gap: 28px;
    }
    .close-button {
      top: 24px;
      right: 24px;
      width: 28px;
      height: 28px;
    }
    .modalForm-input {
        padding: 20px 70px 20px 40px;
        font-size: 18px;
    }
    .modalForm-input::placeholder {
        font-size: 18px;
    }
    .modalForm-icon.name {
      width: 19px;
      height: 19px;
      right: 34px;
    }
    .modalForm-icon.email {
      width: 23px;
      height: 18px;
      right: 34px;
    }
    .modalWrapper-btn {
      margin-top: 40px;
    }
    .submitBtn {
      font-size: 18px;
    }
    .btn-signUpIn-ul {
      margin-top: 20px;
      font-size: 18px;
      line-height: 1.33;
    }
  }
  @media screen and (min-width: 768px) and (max-height: 599.8px){
      .autorization-modal-backdrop {
      padding-top: 40px;
    }
      .modal {
        top: 40px;
        left: 50%;
        transform: translateX(-50%);
      }
  }
  @media screen and (min-width: 768px) and (min-height: 600px){
      .modal {
        top: calc((100% + 70px)/2);
        left: 50%;
        transform: translate(-50%, -50%);
      }
  }
//}
