@font-face {
  src: url("DMSans-Bold.306cbdd3.ttf"), url("DMSans-Bold.306cbdd3.ttf");
  font-weight: 700;
  font-display: swap;
  font-style: normal;
}

@font-face {
  src: url("DMSans-Medium.b8d1a1bf.ttf"), url("DMSans-Medium.b8d1a1bf.ttf");
  font-weight: 500;
  font-display: swap;
  font-style: normal;
}

@font-face {
  src: url("DMSans-Regular.331f979e.ttf"), url("DMSans-Regular.331f979e.ttf");
  font-weight: 400;
  font-display: swap;
  font-style: normal;
}

@font-face {
  src: url("DMSans-Italic.ed07c4fe.ttf"), url("DMSans-Italic.ed07c4fe.ttf");
  font-weight: 400;
  font-display: swap;
  font-style: italic;
}

@font-face {
  src: url("Bauhaus-93-Regular.6937a785.ttf"), url("Bauhaus-93-Regular.6937a785.ttf");
  font-weight: 400;
  font-display: swap;
  font-style: normal;
}

:root {
  --logo-theme-light: #000;
  --logo-theme-dark: #f3f3f3;
  --dark-theme-background: #202024;
  --light-theme-background: #f6f6f6;
  --light-theme-fifty: rgba(17, 17, 17, .5);
  --text-dark-color: #111;
  --text-light-color: #b4afaf;
  --text-white-color: #fff;
  --text-white-color-thirty: rgba(255, 255, 255, .3);
  --text-white-color-fifty: rgba(255, 255, 255, .5);
  --text-white-color-seventy: rgba(255, 255, 255, .7);
  --accent-color1: #4f2ee8;
  --accent-color1-hf: #37209d;
  --accent-color2: #eac645;
  --accent-color3: #fff;
  --accent-color4-hf: #a1a1a1;
  --auth-icon-color1: var(--text-dark-color);
  --auth-icon-color2: var(--accent-color1);
  --auth-icon-color3: var(--text-white-color);
  --amazon-path1-color: #909090;
  --amazon-path2-color: var(--text-dark-color);
  --scrollbar-light-color: #e0e0e0;
  --scrollbar-dark-color: #1f1d1d;
  --add-photo-btn-bgcolor: #888;
  --add-photo-btn-bgcolor-hover: #6a6a6a;
  --add-photo-btn-color: #f3f3f3;
  --main-font: "DM Sans";
  --transition: 500ms cubic-bezier(.4, 0, .2, 1);
  --transition2: 300ms cubic-bezier(.4, 0, .2, 1);
  --transition-shift-right: transform 600ms linear;
  --transition-shift-up: transform 600ms linear;
}

html {
  min-width: 200px;
}

body {
  letter-spacing: .02em;
  background-color: var(--light-theme-background);
  font-family: DM Sans, sans-serif;
  position: relative;
  overflow-x: hidden;
}

.list {
  list-style: none;
}

.link {
  font-family: inherit;
  text-decoration: none;
}

p {
  margin-top: 0;
  margin-bottom: 0;
}

ul, ol {
  margin: 0;
  padding-left: 0;
}

h1, h2, h3, h4, h5, h6 {
  font-family: var(--main-font), sans-serif;
  margin-top: 0;
  margin-bottom: 0;
}

img {
  max-width: 100%;
  height: auto;
  display: block;
}

.is-hidden {
  opacity: 0;
  visibility: hidden;
  pointer-events: none;
  display: none;
}

.visually-hidden {
  width: 1px;
  height: 1px;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  border: 0;
  margin: -1px;
  padding: 0;
  overflow: hidden;
}

.container {
  flex-direction: column;
  margin-left: auto;
  margin-right: auto;
  padding-left: 20px;
  padding-right: 20px;
  display: flex;
}

.sidebar {
  flex-direction: column;
  row-gap: 40px;
  display: flex;
}

@media screen and (max-width: 767.8px) {
  .container {
    max-width: 375px;
    width: 100%;
  }
}

@media screen and (min-width: 768px) and (max-width: 1439.8px) {
  .container {
    max-width: 768px;
    padding-left: 32px;
    padding-right: 32px;
  }

  .sidebar {
    flex-direction: row;
    justify-content: space-between;
    margin-left: 0;
  }
}

@media screen and (min-width: 1440px) {
  .container {
    max-width: 1440px;
    flex-direction: row;
    padding-left: 24px;
    padding-right: 24px;
  }

  .sidebar {
    flex-direction: column;
    row-gap: 86px;
  }
}

.header {
  z-index: 1100;
  background-color: var(--accent-color3);
  transition: background-color var(--transition);
  border: 1.5px solid #111;
  border-radius: 0 0 8px 8px;
  position: sticky;
  top: 0;
  right: 0;
}

.header .container {
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  position: relative;
}

.logo-and-navigation {
  align-items: center;
  display: flex;
}

.navigation.is-hidden {
  display: none;
}

.log-div {
  width: 28px;
  height: 28px;
}

.burger-menu-btn {
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0);
  border: none;
  padding: 0;
  line-height: 0;
}

.burger-menu-icon, .close-mobile-menu-icon {
  stroke: #000;
}

.logo-link {
  letter-spacing: -.04em;
  color: var(--logo-theme-light);
  align-items: center;
  gap: 8px;
  font-weight: 600;
  display: flex;
}

.switch-burger-login {
  align-items: center;
  display: flex;
}

.is-hidden {
  opacity: 0;
  visibility: hidden;
  pointer-events: none;
}

.switch {
  height: 20px;
  margin-left: auto;
  display: inline-block;
  position: relative;
}

.switch input {
  display: none;
}

.slider {
  cursor: pointer;
  -webkit-transition: var(--transition);
  transition: var(--transition);
  background: linear-gradient(#4f2ee8 0%, #dcdcdc 100%);
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  box-shadow: inset 1px 1px 2px rgba(0, 0, 0, .1);
}

.slider:before {
  content: "";
  height: 18px;
  width: 18px;
  background-color: var(--accent-color3);
  -webkit-transition: var(--transition);
  transition: var(--transition);
  position: absolute;
  top: 1px;
  bottom: 1px;
  left: 1px;
}

input:checked + .slider, input:checked + .slider {
  background: linear-gradient(#4f2ee8 0%, #686868 100%);
  box-shadow: inset 1px 1px 2px rgba(0, 0, 0, .1);
}

.slider.round {
  border-radius: 34px;
}

.slider.round:before {
  border-radius: 50%;
}

.shopping-link {
  position: relative;
}

.home-link:hover, .home-link:focus, .home-link.selected, .shopping-link:hover, .shopping-link:focus, .shopping-link.selected {
  background-color: var(--accent-color2);
  font-weight: 700;
}

.shopping-bag-icon {
  fill: var(--text-dark-color);
  stroke: none;
  transition: fill var(--transition), stroke var(--transition);
}

.shopping-link.selected .shopping-bag-icon, .shopping-link:hover .shopping-bag-icon, .shopping-link:focus .shopping-bag-icon {
  fill: var(--text-dark-color);
  stroke: var(--text-dark-color);
  stroke-width: .2px;
}

.ordered-amount-box {
  background-color: var(--accent-color1);
  text-align: center;
  width: 17px;
  height: 17px;
  border-radius: 50%;
  padding: 2px;
  display: none;
  position: absolute;
  bottom: 0;
  right: 8px;
}

.ordered-amount {
  color: var(--accent-color3);
  font-size: 10px;
  display: inline-flex;
  transform: translateY(-3px);
}

.auth-btn {
  cursor: pointer;
  color: var(--text-white-color);
  background-color: var(--accent-color1);
  transition: background-color var(--transition);
  border: none;
  border-radius: 36px;
  align-items: center;
  padding: 4px 12px;
  font-size: 18px;
  font-weight: 700;
  line-height: 1.27;
  display: flex;
  position: relative;
  overflow-x: hidden;
}

.auth-btn:hover, .auth-btn:focus {
  background-color: var(--accent-color1-hf);
}

.auth-btn.is-hidden {
  display: none;
}

.icon-arrow-right {
  stroke: var(--accent-color2);
}

.user-img-div {
  width: 37px;
  height: 37px;
  background-color: var(--accent-color3);
  --auth-icon-color: var(--auth-icon-color2);
  border-radius: 50%;
  margin-right: 9px;
  overflow: hidden;
}

.user-img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.login-p {
  width: 88px;
  white-space: nowrap;
  text-transform: capitalize;
  margin-right: 4px;
  font-size: 18px;
  font-weight: 700;
  line-height: 1.32;
  overflow-x: hidden;
}

.login-p:not(:lang(ae)):not(:lang(ar)):not(:lang(arc)):not(:lang(bcc)):not(:lang(bqi)):not(:lang(ckb)):not(:lang(dv)):not(:lang(fa)):not(:lang(glk)):not(:lang(he)):not(:lang(ku)):not(:lang(mzn)):not(:lang(nqo)):not(:lang(pnb)):not(:lang(ps)):not(:lang(sd)):not(:lang(ug)):not(:lang(ur)):not(:lang(yi)) {
  text-align: left;
}

.login-p:is(:lang(ae), :lang(ar), :lang(arc), :lang(bcc), :lang(bqi), :lang(ckb), :lang(dv), :lang(fa), :lang(glk), :lang(he), :lang(ku), :lang(mzn), :lang(nqo), :lang(pnb), :lang(ps), :lang(sd), :lang(ug), :lang(ur), :lang(yi)) {
  text-align: right;
}

.login-p::-webkit-scrollbar {
  width: .1px;
  height: .1px;
  background-color: rgba(0, 0, 0, 0);
}

.login-p::-webkit-scrollbar-thumb {
  background-color: rgba(0, 0, 0, 0);
}

.auth-triangle-icon-div {
  width: 23px;
  height: 26px;
  background-color: var(--accent-color1);
  justify-content: center;
  align-items: center;
  padding: 9px 5px;
  display: flex;
}

.auth-btn .auth-triangle-icon-div {
  transition: background-color var(--transition);
}

.auth-btn:hover .auth-triangle-icon-div, .auth-btn:focus .auth-triangle-icon-div {
  background-color: var(--accent-color1-hf);
}

.auth-triangle-icon {
  width: 15px;
  height: 8px;
  fill: var(--accent-color3);
  stroke: var(--accent-color3);
}

@media screen and (max-width: 189.8px) {
  .header .container {
    column-gap: 15px;
  }

  .logo-link {
    font-size: 14px;
  }
}

@media screen and (min-width: 190px) and (max-width: 249.8px) {
  .logo-link {
    font-size: 13px;
  }
}

@media screen and (max-width: 249.8px) {
  .switch {
    width: 30px;
    height: 15px;
  }

  .slider:before {
    height: 14px;
    width: 14px;
    top: .5px;
  }

  .switch {
    width: 28px;
    margin-right: 10px;
  }

  input:checked + .slider:before {
    transform: translateX(14px);
  }
}

@media screen and (min-width: 250px) {
  .switch {
    width: 40px;
    margin-right: 14px;
  }

  input:checked + .slider:before {
    transform: translateX(20px);
  }
}

@media screen and (max-width: 767.8px) {
  .header {
    padding-top: 17px;
    padding-bottom: 16px;
  }

  .header .navigation, .header .auth-btn, .header .user-login-btn {
    display: none;
  }
}

@media screen and (min-width: 768px) {
  .header {
    padding-top: 11px;
    padding-bottom: 10px;
  }

  .logo-and-navigation {
    column-gap: 40px;
  }

  .navigation {
    display: block;
  }

  .nav-list {
    align-items: center;
    gap: 8px;
    display: flex;
  }

  .switch {
    margin-right: 24px;
  }

  .log-div {
    min-width: 184px;
    height: 45px;
    align-items: center;
    display: flex;
  }

  .burger-menu-btn, .mobile-menu {
    display: none;
  }

  .home-link, .shopping-link {
    letter-spacing: -.04em;
    color: var(--text-dark-color);
    transition: background-color var(--transition), color var(--transition), font-weight var(--transition);
    border-radius: 36px;
    gap: 8px;
    padding: 8px 16px;
    font-size: 14px;
    font-weight: 500;
    line-height: 1.28;
    display: inline-flex;
  }

  .shopping-bag-icon {
    line-height: 0;
  }

  .user-login-btn {
    background: var(--accent-color1);
    cursor: pointer;
    letter-spacing: -.04em;
    color: var(--accent-color3);
    border: 1.5px solid var(--text-dark-color);
    transition: background-color var(--transition);
    border-radius: 36px;
    justify-content: space-between;
    align-items: center;
    column-gap: 90px;
    padding: 14px;
    font-size: 14px;
    font-weight: 500;
    line-height: 1.29;
    display: flex;
  }

  .user-login-btn:hover, .user-login-btn:focus {
    background-color: var(--accent-color1-hf);
  }

  .user-login-btn.is-hidden {
    display: none;
  }

  .icon-user-wrapper {
    width: 37px;
    height: 37px;
    background: var(--light-theme-background);
    border-radius: 50%;
    justify-content: center;
    align-items: center;
    margin-right: 9px;
    display: inline-flex;
  }

  .icon-registration-triangle {
    margin-left: 4px;
  }
}

@media screen and (min-width: 1440px) {
  .logo-and-navigation {
    column-gap: 76px;
  }

  .login-p {
    max-width: 250px;
    font-size: 22px;
  }
}

.main {
  min-height: calc(100vh - 152px);
  padding-top: 40px;
  padding-bottom: 40px;
}

.main .container {
  min-height: calc(100vh - 238px);
  gap: 40px;
  display: flex;
  position: relative;
}

.logout-modal-backdrop, .user-profile-modal-backdrop, .book-modal-backdrop, .logout-modal-backdrop {
  z-index: 1100;
  width: 100%;
  height: 100%;
  background-color: rgba(17, 17, 17, .4);
  padding: 20px;
  position: fixed;
  top: 0;
  left: 0;
  overflow-y: auto;
}

.category-list-box {
  height: 100%;
  margin: 0;
  padding: 0;
  position: relative;
  overflow-x: auto;
  overflow-y: scroll;
}

.category-list-box::-webkit-scrollbar {
  width: 8px;
  height: 8px;
  background-color: var(--scrollbar-light-color);
}

.category-list-box::-webkit-scrollbar-thumb {
  background-color: var(--scrollbar-light-color);
  border-radius: 12em;
}

.category-list-box-not-empty::-webkit-scrollbar {
  background-color: rgba(0, 0, 0, 0);
}

.category-list {
  margin-right: 20px;
}

.category-list-item {
  letter-spacing: -.02em;
  color: rgba(17, 17, 17, .6);
  cursor: pointer;
  transition: color var(--transition), font-weight var(--transition);
  font-weight: normal;
  line-height: 1.33;
}

.category-list-item:hover, .category-list-item:focus {
  color: var(--accent-color1);
  text-transform: uppercase;
  font-weight: 700;
}

.category-list-item.active {
  text-transform: uppercase;
  color: var(--accent-color1);
  font-weight: 700;
}

.books-box {
  max-width: 1002px;
  width: 100%;
  height: auto;
  min-height: 200px;
  position: relative;
}

.error-box {
  margin-left: auto;
  margin-right: auto;
  padding: 20px;
}

.category-list-box .error-box, .books-box .error-box {
  top: 25%;
}

.error-box-text {
  letter-spacing: .02em;
  text-align: center;
  color: var(--text-light-color);
  font-size: 16px;
  line-height: 1.24;
}

.category-books-list {
  flex-wrap: wrap;
  gap: 40px 25px;
  display: flex;
}

.category-books-list .item-book {
  flex-basis: 100%;
}

.best-book-container {
  flex-direction: column;
  display: flex;
}

.best-book-container:not(:last-child) {
  margin-bottom: 40px;
}

.img-book {
  border-radius: 8px;
  margin-left: auto;
  margin-right: auto;
}

.list-books {
  letter-spacing: -.04em;
  list-style: none;
}

.title-theme-book {
  letter-spacing: -.04em;
  color: var(--text-dark-color);
  margin-bottom: 40px;
  font-weight: 700;
  line-height: 1.08;
}

.last-word-color {
  color: var(--accent-color1);
}

.theme-book {
  letter-spacing: .03em;
  text-transform: uppercase;
  color: var(--text-light-color);
  margin-bottom: 18px;
  font-size: 14px;
  font-weight: 400;
}

.title-book {
  letter-spacing: -.04em;
  text-transform: uppercase;
  color: var(--text-dark-color);
  margin-bottom: 4px;
  font-size: 16px;
  font-weight: 700;
  line-height: .9;
}

.title-author {
  letter-spacing: -.04em;
  color: var(--text-light-color);
  font-size: 12px;
  font-style: italic;
  line-height: 14px;
}

.button-more {
  letter-spacing: -.01em;
  text-transform: uppercase;
  color: var(--text-dark-color);
  cursor: pointer;
  border: 2px solid var(--accent-color1);
  transition: background-color var(--transition), color var(--transition);
  background-color: rgba(0, 0, 0, 0);
  border-radius: 40px;
  align-self: flex-end;
  padding: 14px 28px;
  font-size: 14px;
  font-weight: 700;
  line-height: 18px;
}

.button-more:hover {
  background-color: var(--accent-color1);
  color: var(--accent-color3);
}

.btn-up-scroll {
  cursor: pointer;
  width: 54px;
  height: 54px;
  background-color: var(--accent-color1);
  transition: opacity var(--transition), visibility var(--transition), transform var(--transition);
  border-color: rgba(0, 0, 0, 0);
  border-radius: 50%;
  justify-content: center;
  align-items: center;
  display: flex;
  position: fixed;
  bottom: 90px;
  right: 74px;
}

.btn-up-scroll:before {
  content: "";
  width: 54px;
  height: 54px;
  transition: transform var(--transition);
  background: rgba(79, 46, 232, .3);
  border-radius: 50%;
  position: absolute;
}

.scrol-up-arrow {
  z-index: 100;
  width: 24px;
  height: 24px;
  pointer-events: none;
  fill: #fff;
  stroke: #fff;
  position: absolute;
  top: 0;
  transform: translateY(50%);
}

.btn-up-scroll:hover:before, .btn-up-scroll:focus:before {
  transform: scale(1.2);
}

.btn-up-scroll.is-hidden-btn {
  visibility: hidden;
  opacity: 0;
  pointer-events: none;
  transform: translateY(100%);
}

.img-owerlay {
  border-radius: 8px;
  margin-bottom: 14px;
  position: relative;
  overflow: hidden;
}

.owerlay {
  background-color: var(--accent-color2);
  background-blend-mode: soft-light;
  width: 100%;
  height: 70px;
  transition: transform var(--transition);
  cursor: pointer;
  justify-content: center;
  align-items: center;
  margin: auto;
  display: inline-flex;
  position: absolute;
  bottom: 0;
  left: 0;
  transform: translateY(100%);
}

.item-book:hover .owerlay, .item-book:focus .owerlay {
  transform: translateY(0);
}

.owerlay-content {
  letter-spacing: -.04em;
  text-transform: uppercase;
  color: var(--text-dark-color);
  font-size: 14px;
  font-weight: 500;
  line-height: .8;
}

.off-books {
  height: 100%;
  width: 100%;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  display: flex;
}

.off-books-text {
  letter-spacing: -.04em;
  text-align: center;
  color: var(--text-light-color);
  font-size: 14px;
  font-weight: 500;
  line-height: .8;
}

.support-ukraine-box {
  background: radial-gradient(183.6% 183.01% at 59.76% -23.42%, #4f2ee8 18.03%, #fff 100%);
  border-radius: 16px;
  padding-top: 24px;
  padding-bottom: 20px;
  box-shadow: 0 10px 10px rgba(245, 245, 247, .1);
}

.support-ukraine-text-block {
  justify-content: flex-start;
  align-items: center;
  gap: 12px;
  padding: 0;
  display: flex;
}

.support-ukraine-title {
  letter-spacing: -.04em;
  color: var(--accent-color3);
  white-space: nowrap;
  margin: 0;
  line-height: 1.14;
}

.svg-support-ukraine {
  fill: var(--accent-color3);
  max-width: 20px;
  max-height: 32px;
  fill: var(--text-white-color);
}

.support-ukraine-gallery {
  list-style: none;
}

.support_gallery_item {
  height: 32px;
  color: var(--accent-color3);
  justify-content: flex-start;
  align-items: center;
  font-size: 14px;
  line-height: 1.29;
  display: flex;
}

.support_gallery_number {
  letter-spacing: -.04em;
  width: 14px;
  height: 18px;
  margin-right: 16px;
  font-size: 14px;
  line-height: 1.29;
}

.support_gallery_link img {
  height: 32px;
  width: auto;
  object-fit: contain;
  display: block;
}

.support-ukraine-box-slider {
  max-width: 159px;
  height: 292px;
  margin-top: 40px;
  overflow: hidden;
}

.slider-line {
  flex-flow: column wrap;
  gap: 20px;
  transition: all 1s;
  display: flex;
  position: relative;
  bottom: 0;
}

.shoping-wrapper {
  min-height: 100vh;
  flex-direction: column;
  display: flex;
